.cardWrappper {
  display: flex;
  margin: 0 1rem;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 1rem;
}
.infoWrapper {
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.infoWrapper p {
  margin: 0;
  font-size: 15px;
  color: #666;
}
.infoWrapper h3 {
  margin: 0;
  padding: 0;
  font-size: 16px;
}
